@font-face {
    font-family : 'icons';
    src : url('../fonts/icons.eot?69312537');
    src : url('../fonts/icons.eot?69312537#iefix') format('embedded-opentype'), url('../fonts/icons.woff?69312537') format('woff'), url('../fonts/icons.ttf?69312537') format('truetype'), url('../fonts/icons.svg?69312537#icons') format('svg');
    font-weight : normal;
    font-style : normal;
}

html {
    font-family : 'Droid Sans', sans-serif;
    height : 100%;
}

body {
    height : 100%;
    display : -webkit-box;
    display : -ms-flexbox;
    display : flex;
    -webkit-box-align : center;
        -ms-flex-align : center;
            align-items : center;
}

.container {
    width : 100%;
    text-align : center;
    overflow : hidden;
    margin : 0 20px 0 20px;
}

.sky {
    background-color : #000;
    background-size : cover;
    position : absolute;
    top : 0;
    left : 0;
    height : 100%;
    width : 100%;
    z-index : -1;
}

.stars {
    display : block;
    position : absolute;
    top : 0;
    left : 0;
    height : 100%;
    width : 100%;
    z-index : -1;
}
.stars:first-child {
    background-image : url('../img/big-stars.png');
    -webkit-animation : big-bg 2s linear;
            animation : big-bg 2s linear;
}

@-webkit-keyframes big-bg {
    0% {
        top : 30px;
        left : 30px;
    }
    100% {
        top : 0;
        left : 0;
    }
}

@keyframes big-bg {
    0% {
        top : 30px;
        left : 30px;
    }
    100% {
        top : 0;
        left : 0;
    }
}
.stars:nth-child(2) {
    background-image : url('../img/average-stars.png');
    -webkit-animation : av-bg 2s linear;
            animation : av-bg 2s linear;
}

@-webkit-keyframes av-bg {
    0% {
        top : 30px;
        right : 30px;
    }
    100% {
        top : 0;
        right : 0;
    }
}

@keyframes av-bg {
    0% {
        top : 30px;
        right : 30px;
    }
    100% {
        top : 0;
        right : 0;
    }
}
.stars:last-child {
    background-image : url('../img/little-stars.png');
    -webkit-animation : lt-bg 2s linear;
            animation : lt-bg 2s linear;
}

@-webkit-keyframes lt-bg {
    0% {
        top : -30px;
        left : -30px;
    }
    100% {
        top : 0;
        left : 0;
    }
}

@keyframes lt-bg {
    0% {
        top : -30px;
        left : -30px;
    }
    100% {
        top : 0;
        left : 0;
    }
}
@media (max-width: 480px) {
    .stars:first-child {
        -webkit-animation : none;
                animation : none;
    }
    .stars:nth-child(2) {
        -webkit-animation : none;
                animation : none;
    }
    .stars:last-child {
        -webkit-animation : none;
                animation : none;
    }
}

.page-header {
    color : #fff;
    text-align : center;
    font-weight : bold;
    font-size : 40px;
}
.page-header__name {
    font-size : 100px;
    color : #000;
    text-shadow : -2px -2px 10px yellow, 2px -2px 10px yellow, -2px 2px 10px yellow, 2px 2px 10px yellow;
    opacity : .5;
}
.page-header__prof {
    color : #fff;
    font-size : 50px;
    margin-top : 15px;
}
@media (max-width: 768px) {
    .page-header__name {
        font-size : 70px;
        text-shadow : -1.5px -1.5px 10px yellow, 1.5px -1.5px 10px yellow, -1.5px 1.5px 10px yellow, 1.5px 1.5px 10px yellow;
    }
    .page-header__prof {
        font-size : 35px;
    }
}
@media (max-width: 480px) {
    .page-header__name {
        font-size : 50px;
        text-shadow : -1.5px -1.5px 10px yellow, 1.5px -1.5px 10px yellow, -1.5px 1.5px 10px yellow, 1.5px 1.5px 10px yellow;
    }
    .page-header__prof {
        font-size : 25px;
    }
}

.social {
    display : -webkit-box;
    display : -ms-flexbox;
    display : flex;
    -webkit-box-pack : center;
        -ms-flex-pack : center;
            justify-content : center;
}
.social__item {
    list-style-type : none;
}
.social__link {
    text-decoration : none;
    display : block;
    margin : 20px;
    border : 2px solid #fff;
    border-radius : 100%;
    height : 100px;
    width : 100px;
    text-align : center;
    position : relative;
    -webkit-animation : icons 3s linear;
            animation : icons 3s linear;
}

@-webkit-keyframes icons {
    0% {
        opacity : 0;
    }
    20% {
        opacity : .2;
        box-shadow : 0 0 30px 0 yellow;
    }
    30% {
        opacity : .4;
    }
    65% {
        opacity : 1;
        box-shadow : 0 0 50px 0 yellow;
    }
    100% {
        opacity : 1;
        box-shadow : none;
    }
}

@keyframes icons {
    0% {
        opacity : 0;
    }
    20% {
        opacity : .2;
        box-shadow : 0 0 30px 0 yellow;
    }
    30% {
        opacity : .4;
    }
    65% {
        opacity : 1;
        box-shadow : 0 0 50px 0 yellow;
    }
    100% {
        opacity : 1;
        box-shadow : none;
    }
}
.social__link:hover {
    color : yellow;
    box-shadow : 0 0 50px 0 yellow;
}
.social__link:hover .social__icon {
    color : yellow;
}
.social__icon {
    font-family : 'icons';
    font-style : normal;
    font-weight : normal;
    font-weight : normal;
    color : #fff;
    font-size : 32px;
    position : absolute;
    top : 50%;
    right : 50%;
    -webkit-transform : translate(50%, -50%);
            transform : translate(50%, -50%);
}
.social__icon--gmail {
    font-size : 28px;
}
@media (max-width: 768px) {
    .social__link {
        margin : 15px;
        border : 1.5px solid #fff;
        height : 70px;
        width : 70px;
    }
    @-webkit-keyframes icons {
        20% {
            box-shadow : 0 0 20px 0 yellow;
        }
        65% {
            box-shadow : 0 0 30px 0 yellow;
        }
    }
    @keyframes icons {
        20% {
            box-shadow : 0 0 20px 0 yellow;
        }
        65% {
            box-shadow : 0 0 30px 0 yellow;
        }
    }
    .social__link:hover {
        box-shadow : 0 0 30px 0 yellow;
    }
    .social__icon {
        font-size : 22px;
    }
    .social__icon--gmail {
        font-size : 19px;
    }
}
@media (max-width: 480px) {
    .social__link {
        margin : 10px;
        border : 1px solid #fff;
        height : 50px;
        width : 50px;
        box-shadow : 0 0 20px 0 yellow;
        -webkit-animation : hid 3s linear;
                animation : hid 3s linear;
    }
    @-webkit-keyframes hid {
        10% {
            opacity : 0;
        }
        to {
            opacity : 1;
        }
    }
    @keyframes hid {
        10% {
            opacity : 0;
        }
        to {
            opacity : 1;
        }
    }
    .social__link:hover {
        box-shadow : 0 0 20px 0 yellow;
    }
    .social__icon {
        font-size : 16px;
    }
    .social__icon--gmail {
        font-size : 14px;
    }
}

.page-footer {
    padding : 30px 0 30px 0;
}
